.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.a {
  text-decoration: none;
  color: #343a40;
}

.checked {
  background-color: #1dee86;
}

.todo {
  background-color: #ced3d8f8;
}

.in-progress {
  background-color: #edff85f8;
}

.checking {
  background-color: #ff899cf8;
}

.Table {
  padding-top: 10px;
  display: flex;
  flex-direction: row;
  justify-content: left;
}

.Table div {
  overflow: auto;
  display: block;
  min-width: 235px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
